<template>
  <div>
    <b-modal id="delProductModal" centered title="確認刪除此品項？" @ok="deleteProduct">
      <p class="my-4">商品：{{this.tempProduct.title}}</p>
      <p class="my-4">商品：{{this.tempProduct.id}}</p>
      <p class="my-4">注意：此動作無法還原</p>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tempProduct: {
        imageUrl: [],
      },
    };
  },
  props: {
    user: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    getProduct(id) {
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http.get(api).then((res) => {
        this.tempProduct = res.data.data;
        console.log('this.tempProduct', this.tempProduct);
      }).catch((error) => {
        console.log(error);
      });
    },
    deleteProduct() {
      console.log('id', this.tempProduct.id);
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`;
      this.$http.delete(api).then((res) => {
        this.$emit('update');
        console.log(res);
        // 清空，避免ID留在template
        this.tempProduct = { imageUrl: [] };
      }).catch((error) => {
        console.log(error);
      });
    },
  },
};
</script>

