<template>
  <div class="hello">
    <b-modal
    id="productModal"
    size="xl"
    no-close-on-backdrop
    hide-header-close
    title="新增商品"
    @cencel="hideModal"
    @ok="updateProduct">
      <div>
        <b-row>
          <b-col cols="6">
            <div>
            <label for="pic">產品圖片</label>
            <b-form-input id="pic" v-model="tempProduct.imageUrl[0]" placeholder="產品圖片連結">
            </b-form-input>
            </div>
          </b-col>
          <b-col cols="6">
            <div>
            <label for="title">產品名稱</label>
            <b-form-input id="title" type="text" v-model="tempProduct.title" placeholder="名稱">
            </b-form-input>
            <label for="category">分類</label>
            <b-form-select v-model="tempProduct.category" :options="options">
            </b-form-select>
            <label for="origin_price">原價</label>
            <b-form-input
            id="origin_price" type="number" v-model="tempProduct.origin_price" placeholder="原價">
            </b-form-input>
            <label for="price">售價</label>
            <b-form-input id="price" type="number" v-model="tempProduct.price" placeholder="售價">
            </b-form-input>
            <label for="unit">單位</label>
            <b-form-input id="unit" type="text" v-model="tempProduct.unit" placeholder="單位">
            </b-form-input>
            <label for="content">產地</label>
            <b-form-textarea
            id="content" type="text" v-model="tempProduct.content" placeholder="產地">
            </b-form-textarea>
            <label for="description">說明</label>
            <b-form-textarea
            id="description" type="text" v-model="tempProduct.description" placeholder="說明">
            </b-form-textarea>
            </div>
          </b-col>
        </b-row>
      </div>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: 'Product',
  data() {
    return {
      tempProduct: {
        imageUrl: [],
      },
      options: [
        { value: null, text: '請選擇產品分類' },
        { value: '肉', text: '肉類' },
        { value: '蔬菜', text: '蔬菜' },
        { value: '水果', text: '水果' },
        { value: '海鮮', text: '海鮮' },
        { value: '蛋', text: '蛋' },
        { value: 'd', text: 'This one is disabled', disabled: true },
      ],
    };
  },
  props: {
    user: {
      type: Object,
      default() {
        return {};
      },
    },
    isNew: {
      type: Boolean,
      default() {
        return {};
      },
    },
  },
  methods: {
    getProduct(id) {
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http.get(api).then((res) => {
        this.$bvModal.show('productModal');
        this.tempProduct = res.data.data;
        console.log(this.tempProduct);
      }).catch((error) => {
        console.log(error);
      });
    },
    updateProduct() {
      let httpMethod = 'post';
      let api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/`;
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`;
        httpMethod = 'patch';
      }
      console.log(httpMethod);
      this.$http[httpMethod](api, this.tempProduct).then((res) => {
        console.log(res);
        this.tempProduct = { imageUrl: [] };
        this.$bvModal.hide('productModal');
        this.$emit('update');
      }).catch((error) => {
        console.log(error);
      });
    },
    hideModal() {
      this.$bvModal.hide('productModal');
      this.tempProduct = { imageUrl: [] };
      console.log('hideModal');
    },
  },
};
</script>

