<template>
  <div>
    <h2>產品列表</h2>
    <div>
      <b-container>
        <b-button id="show-btn" variant="outline-primary"
        @click="openModel('new', item)">新增商品</b-button>
        <table role="table" aria-busy="false" aria-colcount="3"
        class="table b-table table-striped table-hover" id="__BVID__781">
          <thead role="rowgroup" class="">
            <tr role="row" class="">
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div>分類</div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div>產品圖片</div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div>產品名</div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div>價錢</div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div>敘述(產地)</div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div>是否上架</div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div></div>
              </th>
              <th role="columnheader" scope="col" aria-colindex="1" class="">
                <div></div>
              </th>
            </tr>
          </thead>
          <tbody role="rowgroup">
            <tr role="row" class="" v-for="item in products" :key="item.id">
              <td aria-colindex="1" role="cell" class="">{{item.category}}</td>
              <td aria-colindex="2" role="cell" class="" style="max-width: 150px;">
                <img :src = "item.imageUrl[0]" alt="" style="width: 100%;">
              </td>
              <td aria-colindex="3" role="cell" class="">{{item.title}}</td>
              <td aria-colindex="4" role="cell" class="">{{item.price}}</td>
              <td aria-colindex="5" role="cell" class="">{{item.content}}</td>
              <td aria-colindex="6" role="cell" class="">{{item.enabled}}</td>
              <td aria-colindex="7" role="cell" class="">
              <b-button
                size="sm"
                class=""
                @click="showDelModal(item)"
                variant="outline-secondary">刪除
              </b-button>
              </td>
              <td aria-colindex="7" role="cell" class="">
              <b-button
                size="sm"
                class=""
                @click="openModel('edit', item)"
                variant="outline-info">編輯
              </b-button>
              </td>
            </tr>
          </tbody>
          </table>
      </b-container>
    </div>
    <product-model
    ref="productModel" :is-new="isNew" @update="getProducts">
    </product-model>
    <delete-product-model
    ref="delProductModal" @update="getProducts">
    </delete-product-model>
  </div>
</template>

<script>
import productModel from '@/components/Productmodal.vue';
import deleteProductModel from '@/components/Delmodal.vue';

export default {
  components: {
    productModel, deleteProductModel,
  },
  data() {
    return {
      products: [],
      token: '',
      tempProduct: {},
      item: {},
      isNew: false,
    };
  },
  created() {
    this.token = document.cookie.replace(/(?:(?:^|.*;\s*)token\s*=\s*([^;]*).*$)|^.*$/, '$1');
    this.$http.defaults.headers.common.Authorization = `Bearer ${this.token}`;
    this.getProducts();
  },
  methods: {
    getProducts(page = 1) {
      this.$http.get(`${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/products`)
        .then((res) => {
          this.products = res.data.data;
          console.log(page);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    showModal() {
      this.$bvModal.show('productModal');
    },
    hideModal() {
      this.$bvModal.hide('productModal');
    },
    openModel(isNew, item) {
      this.tempProduct = { imageUrl: [] };
      console.log(this.tempProduct);
      switch (isNew) {
        case 'new':
          console.log('new');
          this.tempProduct = { imageUrl: [] };
          console.log(this.tempProduct);
          this.isNew = true;
          this.$bvModal.show('productModal');
          break;
        case 'edit':
          this.tempProduct = { ...item };
          this.$refs.productModel.getProduct(this.tempProduct.id);
          this.isNew = false;
          console.log(this.isNew);
          console.log('edit', this.tempProduct);
          break;
        case 'delete':
          console.log('delete');
          this.tempProduct = { ...item };
          break;
        default:
          break;
      }
    },
    showDelModal(item) {
      this.tempProduct = { ...item };
      console.log('showDelModal', this.tempProduct);
      this.$refs.delProductModal.getProduct(this.tempProduct.id);
      this.$bvModal.show('delProductModal');
    },
  },
};
</script>
